import React, { useState, useEffect } from 'react';
import { Tooltip } from 'react-tooltip';
import { initializeApp } from 'firebase/app';
import { getDatabase, ref, onValue, update } from 'firebase/database';

// Firebase configuration - REPLACE WITH YOUR ACTUAL CONFIG
const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  databaseURL: process.env.REACT_APP_FIREBASE_DATABASE_URL,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const database = getDatabase(app);

// Conference schedule data
const scheduleData = {
  groups: [
    {
      name: 'Group 1',
      time: 'Tues. 2:00pm',
      location: 'XP Auditorium',
      sessions: [
        { id: 'g1-1', department: 'DBA Students', title: 'The Keys To The Kingdom: Access Roles That Work For Your Ministry' },
        { id: 'g1-2', department: 'DEV Tynddle', title: 'Blessed Are The Resilient Systems' },
        { id: 'g1-3', department: 'FIN Kids', title: 'The Digital Offering Plate: How To Set Up Your Giving Page' },
        { id: 'g1-4', department: 'CM Preteen', title: 'Streamline Kids Ministry Media' },
        { id: 'g1-5', department: 'MIN Gatheria', title: 'Form Smarts For Ministry Starts' },
        { id: 'g1-6', department: 'TPB Elledge', title: 'Turning Texts Into Touchpoints' },
        { id: 'g1-7', department: 'PB W.Lobby', title: 'Engagement That Guides Ministry Decisions' },
        { id: 'g1-8', department: 'PA Stu Lobby', title: '2000 Kids And A Whole Lot Of Goldfish: VBS At Scale' }
      ]
    },
    {
      name: 'Group 2',
      time: 'Wed. 10:00am',
      sessions: [
        { id: 'g2-1', department: 'DBA Students', title: '360 Leadership Profiles' },
        { id: 'g2-2', department: 'DEV Tynddle', title: 'Code, Coffee, & Cool Stuff' },
        { id: 'g2-3', department: 'FIN Kids', title: 'Track The Giving, Grow The Mission' },
        { id: 'g2-4', department: 'CM Preteen', title: 'Start The Conversation' },
        { id: 'g2-5', department: 'MIN Gatheria', title: 'Big Faith, Bold Mission' },
        { id: 'g2-6', department: 'TPB Elledge', title: 'Involvement Basics For Every Ministry' },
        { id: 'g2-7', department: 'PB W.Lobby', title: 'Saved By The Shell' },
        { id: 'g2-8', department: 'PA Stu Lobby', title: 'Where Media Meets Ministry' }
      ]
    },
    // Add other groups similarly...
  ]
};

function ConferenceSchedule() {
  const [selectedDepartments, setSelectedDepartments] = useState([]);
  const [selectedSessions, setSelectedSessions] = useState([]);
  const [sessionCounts, setSessionCounts] = useState({});

  // Fetch session attendance counts from Firebase
  useEffect(() => {
    const countsRef = ref(database, 'sessionCounts');
    const unsubscribe = onValue(countsRef, (snapshot) => {
      const data = snapshot.val() || {};
      setSessionCounts(data);
    });

    return () => unsubscribe();
  }, []);

  // Toggle session selection
  const toggleSession = (session, group) => {
    const sessionKey = session.id;
    
    // Check if session is already selected
    const isSelected = selectedSessions.some(s => s.id === sessionKey);

    if (isSelected) {
      // Remove session if already selected
      setSelectedSessions(prev => 
        prev.filter(s => s.id !== sessionKey)
      );

      // Decrement session count in Firebase
      const countRef = ref(database, `sessionCounts/${sessionKey}`);
      update(countRef, { count: Math.max(0, (sessionCounts[sessionKey]?.count || 1) - 1) });
    } else {
      // Add session
      const newSession = {
        ...session,
        groupName: group.name,
        groupTime: group.time
      };
      setSelectedSessions(prev => [...prev, newSession]);

      // Increment session count in Firebase
      const countRef = ref(database, `sessionCounts/${sessionKey}`);
      update(countRef, { count: (sessionCounts[sessionKey]?.count || 0) + 1 });
    }
  };

  // Department filtering
  const toggleDepartment = (department) => {
    setSelectedDepartments(prev => 
      prev.includes(department)
        ? prev.filter(d => d !== department)
        : [...prev, department]
    );
  };

  // Get unique departments
  const getDepartments = () => {
    return [...new Set(scheduleData.groups.flatMap(group => 
      group.sessions.map(session => session.department)
    ))];
  };

  // Filter sessions based on department selection
  const isSessionVisible = (session) => {
    return selectedDepartments.length === 0 || 
           selectedDepartments.includes(session.department);
  };

  // Share current schedule
  const shareSchedule = () => {
    const selectedSessionsQuery = selectedSessions
      .map(session => encodeURIComponent(JSON.stringify({
        id: session.id,
        department: session.department,
        title: session.title,
        groupName: session.groupName,
        groupTime: session.groupTime
      })))
      .join(',');

    const shareUrl = `${window.location.origin}${window.location.pathname}?sessions=${selectedSessionsQuery}`;
    
    // Copy to clipboard
    navigator.clipboard.writeText(shareUrl).then(() => {
      alert('Shareable link copied to clipboard!');
    }).catch(err => {
      console.error('Could not copy text: ', err);
    });
  };

  return (
    <div className="conference-schedule">
      <h1>Conference Schedule</h1>
      
      {/* Department Filter */}
      <div className="department-filter">
        <h3>Filter by Department</h3>
        {getDepartments().map(department => (
          <button
            key={department}
            onClick={() => toggleDepartment(department)}
            className={selectedDepartments.includes(department) ? 'active' : ''}
          >
            {department}
          </button>
        ))}
        <button onClick={shareSchedule} className="share-btn">
          Share Selected Schedule
        </button>
      </div>

      {/* Selected Sessions */}
      {selectedSessions.length > 0 && (
        <div className="selected-sessions">
          <h3>My Selected Sessions</h3>
          {selectedSessions.map((session, index) => (
            <div key={index} className="selected-session">
              <span className="department">{session.department}</span>
              <span className="title">{session.title}</span>
              <span className="group-info">{session.groupName} - {session.groupTime}</span>
            </div>
          ))}
        </div>
      )}

      {/* Schedule Groups */}
      {scheduleData.groups.map(group => (
        <div key={group.name} className="schedule-group">
          <h2>{group.name} - {group.time} {group.location ? `(${group.location})` : ''}</h2>
          
          <div className="sessions">
            {group.sessions
              .filter(isSessionVisible)
              .map((session) => {
                const sessionCount = sessionCounts[session.id]?.count || 0;
                const isSelected = selectedSessions.some(s => s.id === session.id);

                return (
                  <div 
                    key={session.id} 
                    className={`session ${isSelected ? 'selected' : ''}`}
                    onClick={() => toggleSession(session, group)}
                    data-tooltip-id={`session-tooltip-${session.id}`}
                    data-tooltip-content={session.title}
                  >
                    {sessionCount > 0 && (
                      <span className="session-count">{sessionCount}</span>
                    )}
                    <span className="department">{session.department}</span>
                    <span className="title">{session.title}</span>
                    <Tooltip 
                      id={`session-tooltip-${session.id}`} 
                      place="top"
                    />
                  </div>
                );
              })
            }
          </div>
        </div>
      ))}

      <style jsx global>{`
        .conference-schedule {
          max-width: 1000px;
          margin: 0 auto;
          padding: 20px;
          font-family: Arial, sans-serif;
        }
        .department-filter {
          display: flex;
          flex-wrap: wrap;
          gap: 10px;
          margin-bottom: 20px;
        }
        .department-filter button {
          padding: 5px 10px;
          background-color: #f0f0f0;
          border: 1px solid #ddd;
          border-radius: 4px;
          cursor: pointer;
          transition: background-color 0.3s;
        }
        .department-filter button.active {
          background-color: #4CAF50;
          color: white;
        }
        .share-btn {
          background-color: #007bff;
          color: white;
        }
        .selected-sessions {
          background-color: #f0f0f0;
          padding: 15px;
          margin-bottom: 20px;
          border-radius: 5px;
        }
        .selected-session {
          display: flex;
          flex-direction: column;
          margin-bottom: 10px;
          padding: 10px;
          background-color: white;
          border-radius: 4px;
        }
        .schedule-group {
          border: 1px solid #ddd;
          margin-bottom: 15px;
          border-radius: 5px;
        }
        .sessions {
          display: grid;
          grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
          gap: 10px;
          padding: 15px;
        }
        .session {
          position: relative;
          border: 1px solid #e0e0e0;
          padding: 10px;
          border-radius: 4px;
          cursor: pointer;
          transition: background-color 0.3s;
        }
        .session:hover {
          background-color: #f0f0f0;
        }
        .session.selected {
          background-color: #e6f3e6;
          border-color: #4CAF50;
        }
        .session-count {
          position: absolute;
          top: -10px;
          right: -10px;
          background-color: #ff4500;
          color: white;
          border-radius: 50%;
          width: 24px;
          height: 24px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 12px;
          font-weight: bold;
        }
        .session .department {
          display: block;
          font-weight: bold;
          color: #666;
          margin-bottom: 5px;
        }
        .session .title {
          display: block;
          color: #333;
        }
      `}</style>
    </div>
  );
}

function App() {
  return (
    <div className="app">
      <ConferenceSchedule />
    </div>
  );
}

export default App;